*{box-sizing:border-box;margin:0;padding:0}body{background-color:#ececec;width:100vw;display:flex;justify-content:center;font-family:azo-sans-web,sans-serif;font-weight:100;font-style:normal}

.logo{display:flex;position:relative;justify-content:flex-start;flex-direction:column;width:30.5%;top:40%}.logo-brain,.logo-text{width:100%}.shrink{-webkit-animation:shrinkBrain 1s ease 0s 1 forwards;animation:shrinkBrain 1s ease 0s 1 forwards}@-webkit-keyframes shrinkBrain{0%{opacity:1;-webkit-transform:scale(1);transform:scale(1)}100%{opacity:0;-webkit-transform:scale(0);transform:scale(0);display:none}}@keyframes shrinkBrain{0%{opacity:1;-webkit-transform:scale(1);transform:scale(1)}100%{opacity:0;-webkit-transform:scale(0);transform:scale(0);display:none}}

.player-menu{display:flex;flex-direction:column;justify-content:center;align-items:center;margin:10rem 0 0;width:50%;min-width:200px;text-align:center;transition-duration:1s;opacity:0;-webkit-animation:displayMenu 1s ease .5s 1 forwards;animation:displayMenu 1s ease .5s 1 forwards}.player-menu h2{color:#90989e;font-size:1.8rem;width:100%;margin-bottom:1.25rem;letter-spacing:.125rem;transition-duration:1s}.player-menu .player-menu-btn-one,.player-menu .player-menu-btn-two{padding:.75rem .25rem .5rem;margin:.9rem 0;color:#ffffff;font-family:alternate-gothic-no-3-d,sans-serif;font-weight:400;font-size:1.85rem;border:none;border-radius:30px;width:90%;transition-duration:1s}.player-menu .player-menu-btn-one{background-color:#FF758E}.player-menu .player-menu-btn-two{background-color:#3596E3}.player-menu .player-menu-btn-one:hover,.player-menu .player-menu-btn-two:hover{cursor:pointer}@media (max-height: 930px){.player-menu{margin:6rem 0 0}.player-menu h2{font-size:1.4rem !important}.player-menu .player-menu-btn-one,.player-menu .player-menu-btn-two{font-size:1.35rem !important;margin:.4rem 0 !important}}@media (max-width: 600px){.player-menu h2{font-size:1.4rem !important}.player-menu .player-menu-btn-one,.player-menu .player-menu-btn-two{font-size:1.35rem !important;margin:.5rem 0 !important}}.menu-appear{-webkit-animation:displayMenu 1s ease .5s 1 forwards;animation:displayMenu 1s ease .5s 1 forwards}.menu-vanish{-webkit-animation:hideMenu 1s ease 0s 1 forwards;animation:hideMenu 1s ease 0s 1 forwards}@-webkit-keyframes displayMenu{0%{opacity:0;-webkit-transform:translateY(10rem);transform:translateY(10rem)}100%{opacity:1;-webkit-transform:translateY(0rem);transform:translateY(0rem)}}@keyframes displayMenu{0%{opacity:0;-webkit-transform:translateY(10rem);transform:translateY(10rem)}100%{opacity:1;-webkit-transform:translateY(0rem);transform:translateY(0rem)}}@-webkit-keyframes hideMenu{0%{opacity:1;-webkit-transform:translateY(0rem);transform:translateY(0rem)}100%{opacity:0;-webkit-transform:translateY(10rem);transform:translateY(10rem)}}@keyframes hideMenu{0%{opacity:1;-webkit-transform:translateY(0rem);transform:translateY(0rem)}100%{opacity:0;-webkit-transform:translateY(10rem);transform:translateY(10rem)}}

.round-screen{position:absolute;width:100%;margin-top:50vh;-webkit-transform:translateY(50%);transform:translateY(50%);display:flex;justify-content:center;align-items:center;z-index:500}.round-screen h1{display:block;font-family:alternate-gothic-no-3-d,sans-serif;font-size:1rem;color:#403839;text-shadow:0px 0px 4px #fff;-webkit-animation:new-round 1.75s ease-out 0s forwards;animation:new-round 1.75s ease-out 0s forwards}@-webkit-keyframes new-round{0%{-webkit-transform:scale(1);transform:scale(1);opacity:1}100%{-webkit-transform:scale(9);transform:scale(9);opacity:0}}@keyframes new-round{0%{-webkit-transform:scale(1);transform:scale(1);opacity:1}100%{-webkit-transform:scale(9);transform:scale(9);opacity:0}}

#dashboard{position:relative;text-align:center;width:100%;height:24%}#dashboard .header-logo{width:30.5%;margin:.8rem 0}#dashboard .scoreboard{width:100%;display:flex}#dashboard .scoreboard-p1,#dashboard .scoreboard-p2{width:50%;background-color:#6C7983;color:#9FA7AE;padding:.8rem;transition-duration:1s}#dashboard .scoreboard-p1 h2,#dashboard .scoreboard-p2 h2{font-size:1.2rem}#dashboard .scoreboard-p1 .score,#dashboard .scoreboard-p2 .score{font-family:alternate-gothic-no-3-d,sans-serif;font-size:3.25rem;font-weight:100;line-height:3.25rem;transition-duration:1s}#dashboard .scoreboard-p1 .score .pts,#dashboard .scoreboard-p2 .score .pts{font-size:1.75rem;margin-left:.5rem;transition-duration:1s}#dashboard .scoreboard-p1{margin-right:.225rem;text-align:left;padding-left:2rem;-webkit-animation:slideRt 1s ease 0s 1 forwards;animation:slideRt 1s ease 0s 1 forwards}#dashboard .scoreboard-p2{margin-left:.225rem;text-align:right;padding-right:2rem;-webkit-animation:slideLt 1s ease 0s 1 forwards;animation:slideLt 1s ease 0s 1 forwards}#dashboard .scoreboard .turn{color:#fff}#dashboard .turn-indicators{display:flex;justify-content:space-between;margin-top:.45rem;width:100%;height:.5rem}#dashboard .turn-indicators-p1,#dashboard .turn-indicators-p2{width:50%;height:100%}#dashboard .turn-indicators-p1{margin-right:.225rem;background-color:#FF758E}#dashboard .turn-indicators-p2{margin-left:.225rem;background-color:#3596E3}#dashboard .turn-indicators .inactive{background-color:#90989e !important}#dashboard .clock{position:absolute;width:14vh;height:14vh;min-width:108px;min-height:108px;border:.45rem solid #fff;box-shadow:inset 0px 0px 0px 6px #6C7983;border-radius:50%;left:50%;-webkit-transform:translateX(-50%) translateY(-64%);transform:translateX(-50%) translateY(-64%);background-color:#403839;transition-duration:1s;color:#fff;display:flex;justify-content:center;align-items:center;font-family:alternate-gothic-no-3-d,sans-serif;font-size:2rem}#dashboard .hide{display:none}#dashboard .start{z-index:10;background-color:#75B1BA;box-shadow:inset 0px 0px 0px 6px #6C7983}#dashboard .start:hover{cursor:pointer}.shake{-webkit-animation:shaker 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;animation:shaker 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}@-webkit-keyframes slideRt{0%{-webkit-transform:translateX(-100%);transform:translateX(-100%)}100%{-webkit-transform:translateX(0%);transform:translateX(0%)}}@keyframes slideRt{0%{-webkit-transform:translateX(-100%);transform:translateX(-100%)}100%{-webkit-transform:translateX(0%);transform:translateX(0%)}}@-webkit-keyframes slideLt{0%{-webkit-transform:translateX(100%);transform:translateX(100%)}100%{-webkit-transform:translateX(0%);transform:translateX(0%)}}@keyframes slideLt{0%{-webkit-transform:translateX(100%);transform:translateX(100%)}100%{-webkit-transform:translateX(0%);transform:translateX(0%)}}@-webkit-keyframes shaker{10%,90%{-webkit-transform:translate3d(-1px, 0, 0);transform:translate3d(-1px, 0, 0)}20%,80%{-webkit-transform:translate3d(2px, 0, 0);transform:translate3d(2px, 0, 0)}30%,50%,70%{-webkit-transform:translate3d(-4px, 0, 0);transform:translate3d(-4px, 0, 0)}40%,60%{-webkit-transform:translate3d(4px, 0, 0);transform:translate3d(4px, 0, 0)}}@keyframes shaker{10%,90%{-webkit-transform:translate3d(-1px, 0, 0);transform:translate3d(-1px, 0, 0)}20%,80%{-webkit-transform:translate3d(2px, 0, 0);transform:translate3d(2px, 0, 0)}30%,50%,70%{-webkit-transform:translate3d(-4px, 0, 0);transform:translate3d(-4px, 0, 0)}40%,60%{-webkit-transform:translate3d(4px, 0, 0);transform:translate3d(4px, 0, 0)}}@media (max-height: 1040px){.clock{-webkit-transform:translateX(-50%) translateY(-75%) !important;transform:translateX(-50%) translateY(-75%) !important}}@media (max-height: 940px){.clock{-webkit-transform:translateX(-50%) translateY(-95%) !important;transform:translateX(-50%) translateY(-95%) !important}.start{font-size:1.75rem !important}}@media (max-height: 850px){.scoreboard-p1 h2,.scoreboard-p2 h2{font-size:1rem !important}.scoreboard-p1 .score,.scoreboard-p2 .score{font-size:3rem !important;line-height:2rem}.scoreboard-p1 .score .pts,.scoreboard-p2 .score .pts{font-size:1.5rem !important;margin-left:.25rem !important}.scoreboard-p1{padding-left:1rem !important}.scoreboard-p2{padding-right:1rem !important}}

.card{position:relative;display:flex;justify-content:center;align-items:center;opacity:0;-webkit-animation:dealCard 2s ease .1s 1 forwards;animation:dealCard 2s ease .1s 1 forwards}.card .back{position:absolute;width:100%;height:100%;z-index:300;background-color:#fff;border-radius:12px;box-shadow:0px 2px 6px rgba(0,0,0,0.2);display:flex;justify-content:center;align-items:center;flex-direction:column}.card .back:hover{cursor:pointer;border:1px solid #75B1BA}.card .back .back-img{width:80%}.front{position:absolute;display:flex;justify-content:center;align-items:center;padding:8px;z-index:200;opacity:.65}.clicked{opacity:0}@-webkit-keyframes dealCard{0%{opacity:0;-webkit-transform:rotateY(720deg);transform:rotateY(720deg)}100%{opacity:1;-webkit-transform:rotateY(0deg);transform:rotateY(0deg)}}@keyframes dealCard{0%{opacity:0;-webkit-transform:rotateY(720deg);transform:rotateY(720deg)}100%{opacity:1;-webkit-transform:rotateY(0deg);transform:rotateY(0deg)}}

.game-grid{position:absolute;width:100%;padding:1.25rem;height:calc(100% - 24%);display:grid;justify-content:center;grid-template-columns:repeat(4, 1fr);grid-gap:.9rem}.game-grid .square{border:4px solid #D2D6D9;border-radius:12px}

#points{position:absolute;width:100%;margin-top:50vh;-webkit-transform:translateY(50%);transform:translateY(50%);display:flex;justify-content:center;align-items:center;z-index:500}#points h1{display:block;font-family:alternate-gothic-no-3-d,sans-serif;font-size:1rem;color:#403839;text-shadow:0px 0px 6px #fff;-webkit-animation:pointsReveal 1.25s ease-out 0s forwards;animation:pointsReveal 1.25s ease-out 0s forwards}.plus h1{color:#75B1BA !important}.minus h1{color:#FF758E !important}@-webkit-keyframes pointsReveal{0%{-webkit-transform:scale(4);transform:scale(4);opacity:1}20%{opacity:1}100%{-webkit-transform:scale(20);transform:scale(20);opacity:0}}@keyframes pointsReveal{0%{-webkit-transform:scale(4);transform:scale(4);opacity:1}20%{opacity:1}100%{-webkit-transform:scale(20);transform:scale(20);opacity:0}}

#game-screen{width:100%;height:100%}

#end-game{display:flex;flex-direction:column;justify-content:center;align-items:center;position:absolute;top:0;left:0;width:100%;height:100%;background-color:#3596E3;color:#fff;font-family:alternate-gothic-no-3-d,sans-serif;z-index:5000}#end-game .farewell-message{display:flex;text-align:center;flex-direction:column;align-items:center;justify-content:center;padding:2.5rem}#end-game .farewell-message h1{font-size:5rem;font-weight:300;line-height:3.75rem}#end-game .farewell-message h2{font-size:2.5rem;font-weight:300;line-height:3.75rem}#end-game .farewell-message p{font-weight:100;font-size:1.75rem;letter-spacing:.075rem;margin-top:2rem;opacity:.55}#end-game button{padding:.75rem .25rem .5rem;margin:.9rem 0;color:#fff;background-color:#FF758E;font-family:alternate-gothic-no-3-d,sans-serif;font-weight:400;font-size:1.85rem;border:none;border-radius:30px;width:60%;transition-duration:1s}#end-game button:hover{cursor:pointer}

.learn{position:absolute;width:100%;height:100%;background-color:#fff;color:rgba(64,56,57,0.85);text-align:center;padding:2rem 3rem;transition:all 1s cubic-bezier(0.2, 0.67, 0.19, 0.97);overflow:auto}.learn-section{display:flex;align-items:center;justify-content:flex-start;flex-direction:column;width:100%;padding-bottom:2rem}.learn-section-brain,.learn-section-text{width:24.5%}.learn-content{border-top:1px solid rgba(64,56,57,0.15);margin-top:2rem 0}.learn-content h2{font-size:1.5rem;margin:2rem 0 1.5rem 0}.learn-content-section{margin-bottom:1.25rem}.learn-content-section h3,.learn-content-section p{text-align:left}.learn-content-section h3{font-size:1.15rem;font-weight:bold;margin:0 0 .2rem 0}.learn-content-section p{font-size:1rem}.learn-content-section .deduct{margin-top:1.25rem}.learn-content-section .points-info{display:flex;justify-content:flex-start;align-items:center;width:100%;margin-top:1rem}.learn-content-section .points-info img{align-self:center;width:12.5%}.learn-content-section .points-info-p{text-align:left;margin-left:1.5rem;font-size:1rem}.learn-content-section .points-info-p p{font-size:1.05rem}.learn-content-section .gameplay{margin-top:1rem}.robot{-webkit-transform:scale(0.75);transform:scale(0.75);opacity:.85}.scientist{-webkit-transform:scale(0.85);transform:scale(0.85);opacity:.85}.hide{margin-top:100vh}

#App{position:relative;width:100vw;height:100vh;max-width:calc(100vh * .465);min-width:321.45px;min-height:726px;background-color:#fbfbfb;overflow:hidden}#App #start-screen{position:absolute;margin:0 auto;top:50%;width:100%;-webkit-transform:translateY(-50%);transform:translateY(-50%);display:flex;justify-content:center;align-items:center;flex-direction:column}#App .learn-game{font-size:1.2rem;text-align:center;position:absolute;bottom:8rem;color:#90989e;width:100%;opacity:0;-webkit-animation:howToPlayFadeIn 1.5s ease 1s 1 forwards;animation:howToPlayFadeIn 1.5s ease 1s 1 forwards}#App .learn-game-link{display:inline-block;padding:.5rem .5rem}#App .learn-game-link:hover{cursor:pointer;color:#403839}.title-placer{-webkit-animation:placeTitle 1s ease 1s 1 forwards;animation:placeTitle 1s ease 1s 1 forwards}@-webkit-keyframes placeTitle{0%{top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}100%{top:0%;-webkit-transform:translateY(0%);transform:translateY(0%)}}@keyframes placeTitle{0%{top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}100%{top:0%;-webkit-transform:translateY(0%);transform:translateY(0%)}}@-webkit-keyframes howToPlayFadeIn{0%{opacity:0}100%{opacity:1}}@keyframes howToPlayFadeIn{0%{opacity:0}100%{opacity:1}}

