@import './variables';
@import url("https://use.typekit.net/loc6ewc.css");

#dashboard {
  position: relative;
  text-align: center;
  width: 100%;
  height: $dashboard-height;

  .header-logo {
    width: 30.5%;
    margin: .8rem 0;
  }

  .scoreboard {
    width: 100%;
    display: flex;

    &-p1, &-p2 {
      width: 50%; 
      background-color: #6C7983;
      color: #9FA7AE;
      padding: .8rem;
      transition-duration: 1s;

      h2 {
        font-size: 1.2rem;
      }

      .score {
        font-family: alternate-gothic-no-3-d,sans-serif;
        font-size: 3.25rem;
        font-weight: 100;
        line-height: 3.25rem;
        transition-duration: 1s;
        

        .pts {
          font-size: 1.75rem;
          margin-left: .5rem;
          transition-duration: 1s;
        }
      }
    }

    &-p1 {
      margin-right: .225rem;
      text-align: left;
      padding-left: 2rem;
      animation: slideRt 1s ease 0s 1 forwards;
    }

    &-p2 {
      margin-left: .225rem;
      text-align: right;
      padding-right: 2rem;
      animation: slideLt 1s ease 0s 1 forwards;
    }

    .turn {
      color: #fff;
    }
  }

  /* Player's turn indicator bars */
  .turn-indicators {
    display: flex;
    justify-content: space-between;
    margin-top: .45rem;
    width: 100%;
    height: .5rem;
    

    &-p1, &-p2 {
      width: 50%;
      height: 100%;   
    }

    &-p1 {
      margin-right: .225rem;
      background-color: $coral;
    }

    &-p2 {
      margin-left: .225rem;
      background-color: $blue;
    }
    
    /* Grays out color for indicator */
    .inactive {
      background-color: $gray !important;
    }
  }

  .clock {
    position: absolute;
    width: $clock-size;
    height: $clock-size;
    min-width: 108px;
    min-height: 108px;
    border: .45rem solid #fff;
    box-shadow: inset 0px 0px 0px 6px #6C7983;
    border-radius: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-64%);
    background-color: $black;
    transition-duration: 1s;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: alternate-gothic-no-3-d,sans-serif;
    font-size: 2rem;
  }

  .hide {
    display: none;
  }

  .start {
    z-index: 10;
    background-color: $green;
    box-shadow: inset 0px 0px 0px 6px #6C7983;

    &:hover {
      cursor: pointer;
    }
  }
}

.shake {
  animation: shaker 0.82s cubic-bezier(.36,.07,.19,.97) both;
  transform: translate3d(0, 0, 0);
}

/*** Animations ***/

/* Slides in the scoreboard */
@keyframes slideRt {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0%);
  }
}

@keyframes slideLt {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(0%);
  }
}

/* Shakes score */
@keyframes shaker {
  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }
  
  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}

/*** Media Queries ***/
@media (max-height: 1040px) {
  .clock {
    transform: translateX(-50%) translateY(-75%) !important;
  }
}

@media (max-height: 940px) {
  .clock {
    transform: translateX(-50%) translateY(-95%) !important;
  }

  .start {
    font-size: 1.75rem !important;
  }

  
}


@media (max-height: 850px) {
  .scoreboard {
    &-p1, &-p2 {

      h2 {
        font-size: 1rem !important;
      }

      .score {
        font-size: 3rem !important;
        line-height: 2rem;

        .pts {
          font-size: 1.5rem !important;
          margin-left: .25rem !important;
        }
      }
    }

    &-p1 {
      padding-left: 1rem !important;
    }
    
    &-p2 {
      padding-right: 1rem !important;
    }
  }
}